<template>
   <div>
     <tarjeta-credito :trans='translations' @change="guardar_tarjetas"
     @cardChanged="tipo_tarjeta"></tarjeta-credito>

     <div v-show="error" class="row">
       <div class="col-md-4"></div>
       <div class="col-md-4">
         <div class="alert alert-warning">
           Incorrecto, Verifique los datos de la trajeta
         </div>
       </div>
       <div class="col-md-4"></div>
     </div>
     <div v-show="loading" class="row">
       <div class="col-md-3"></div>
       <div class="col-md-4"> 
        <svg xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" style="margin: auto; background: rgb(241, 242, 243); display: block; shape-rendering: auto; animation-play-state: running; animation-delay: 0s;" width="200px" height="200px" viewBox="0 0 100 100" preserveAspectRatio="xMidYMid">
        <circle cx="50" cy="50" fill="none" stroke="#3c0f0f" stroke-width="10" r="35" stroke-dasharray="164.93361431346415 56.97787143782138" style="animation-play-state: running; animation-delay: 0s;">
        <animateTransform attributeName="transform" type="rotate" repeatCount="indefinite" dur="1s" values="0 50 50;360 50 50" keyTimes="0;1" style="animation-play-state: running; animation-delay: 0s;"></animateTransform>
        </circle>
        </svg>
       </div>
       <div class="col-md-3"></div>
     </div>
     
     <div v-show="resultado==1" class="row">
       <div class="col-md-3"></div>
       <div class="col-md-4">
         <div class="alert alert-success">
           El pago se realizo exitosamente
         </div>
       </div>
       <div class="col-md-3"></div>
     </div>
     
     <div v-show="resultado==2" class="row">
       <div class="col-md-3"></div>
       <div class="col-md-4">
         <div class="alert alert-danger">
           El pago no se realizo, verifique sus datos
         </div>
       </div>
       <div class="col-md-3"></div>
     </div>
     
     <div v-show="resultado==3" class="row">
       <div class="col-md-3"></div>
       <div class="col-md-4">
         <div class="alert alert-danger">
           Hubo un error en el servidor, porfavor espere unos segundos y vuelva a intentar
         </div>
       </div>
       <div class="col-md-3"></div>
     </div>

     <div v-show="boton_visible" class="row">
       <div class="col-md-4"></div>
       <div class="col-md-4">
         <button type="button" @click="pagar" class="btn btn-success">Pagar</button>
       </div>
       <div class="col-md-4"></div>
     </div>
   </div>
</template>

<script>
import VCreditCard from 'v-credit-card';
import 'v-credit-card/dist/VCreditCard.css';
import axios from 'axios';
export default{
  data(){
      return{
      translations:{
           name: {
        label: 'Nombre',
        placeholder: 'Nombre completo'
    },
    card: {
        label: 'Número de tarjeta',
        placeholder: 'Número de tarjeta'
    },
    expiration: {
        label: 'Expiration'
    },
    security: {
        label: 'CCV',
        placeholder: 'Código'
    }
      }

      ,card:{
        name:''
        ,cardNumber:''
        ,expiration:''
        ,security:''
        ,tipo:''

      }
         ,key_public:llave_publica
         ,error:false
        ,token_pago:''
        ,idservicio:servicio
        ,token:laravel_token
        ,url:url_pago
        ,loading:false
        ,pago_terminado:false
        ,resultado:0
      }  
  }
    ,computed:{
    boton_visible:function(){
           var bandera=true;
           if(this.loading)
           bandera=false;

           if(this.pago_terminado)
           bandera=false

           return bandera;
    }
  }
  ,methods:{
    guardar_tarjetas:function(value){
      this.card.name=value.name;
      this.card.cardNumber=value.cardNumber;
      this.card.expiration=value.expiration;
      this.card.security=value.security;
      
    }
    ,tipo_tarjeta:function(tipo){
        this.card.tipo=tipo;
    }

    ,pagar:function(){
      this.error=false;

      if(this.card.name=='')
     this.error=true;

      if(this.card.cardNumber=='')
      this.error=true;

      if(this.card.expiration=='')
      this.error=true;

      if(this.card.security=='')
      this.error=true;

      if(!this.error){
        var temp=this.card.expiration.split('/');
        var self=this;

        this.loading=true;

        Stripe.createToken({
            number:this.card.cardNumber
            ,cvc:this.card.security
            ,exp_month:temp[0]
            ,exp_year:temp[1]
        },function(status,response){
            if(response.error) {
              alert('error al pedir el token');
            }
            else{
              self.token_pago=response['id'];
              var datos = {
                          idservicio:self.idservicio
                          ,_token:self.token
                          ,token_stripe:self.token_pago
                          };
            axios.post(self.url,datos).then((result) =>{
              if(result.data.status=='Error'){
                      //alert(result.data.mensaje);
                      self.resultado=2;
                  }
                  else{
                    self.resultado=1;
                  }
                  self.loading=false;
                  self.pago_terminado=true;
               })
               .catch(function(error){
                 //alert('Hubo un error en el servidor');
                 self.loading=false;
                 self.pago_terminado=true;
                 self.resultado=3;
               })
            }
        })
      }
    }
  }

  ,components:{
    'tarjeta-credito':VCreditCard
  }
  ,created(){
     Stripe.setPublishableKey(this.key_public);
  }
}
</script>

<style lang="scss">

</style>



